<template>
    <li class="cart__item product">
        <div class="product__pic">
        <img :src="item.product.image" width="120" height="120" :alt="item.product.title">
        </div>
        <h3 class="product__title">
        {{ item.product.title }}
        </h3>
        <span class="product__code">
        Артикул: {{ item.productId }}
        </span>

        <div class="product__counter form__counter">
        <button type="button" aria-label="Убрать один товар" @click.prevent="--amount">
            <svg width="10" height="10" fill="currentColor">
            <use xlink:href="#icon-minus"></use>
            </svg>
        </button>

        <input type="text" v-model="amount" name="count">

        <button type="button" aria-label="Добавить один товар" @click.prevent="++amount">
            <svg width="10" height="10" fill="currentColor">
            <use xlink:href="#icon-plus"></use>
            </svg>
        </button>
        </div>

        <b class="product__price">
        {{ (item.product.price * item.amount) | priceFormat}} ₽
        </b>

        <button class="product__del button-del" type="button" aria-label="Удалить товар из корзины" @click.prevent="deleteProduct(item.productId)">
        <svg width="20" height="20" fill="currentColor">
            <use xlink:href="#icon-close"></use>
        </svg>
        </button>
    </li>
</template>

<script>
import priceFormat from '@/helpers/priceFormat'

export default {
    props: ['item'],
    filters: {priceFormat},
    computed: {
        amount: {
            get() {
                return this.item.amount
            },
            set(value) {
                this.$store.dispatch(
                    'updateCartProductAmount',
                    {
                        productId: this.item.productId,
                        amount: value,
                    }
                )
            }
        }
    },
    methods: {
        deleteProduct(productId) {
            this.$store.dispatch('deleteProductFromCart', productId)
        }
    }
}
</script>
